* {
  border: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  outline: 0;
  padding: 0;
  text-decoration: none;
  text-rendering: optimizeLegibility;
}

html,
body {
  min-height: 100vh;
  min-width: 100%;
}

a {
  color: currentColor;
  text-decoration: none;
}

textarea {
  resize: none;
}
